import * as React from 'react';
import { Paper, Table, TableRow, TableBody, Box, TableCell, TableContainer, Typography } from '@mui/material';
import { styled } from '@mui/material/styles';
import { CircleTwoTone } from '@mui/icons-material';

function preventDefault(event: React.MouseEvent) {
  event.preventDefault();
}

interface GPIOPin {
  id: string,
  name: string,
  state: 'in' | 'out' | null,
  type: 'io' | 'power' | 'ground',
}

const gpio: [GPIOPin, GPIOPin][] = [
  [{ id: '1', name: 'pow3v3', state: null, type: 'ground' }, { id: '2', name: 'pow5v', state: 'out', type: 'power' }],
  [{ id: '3', name: 'gpio 2', state: 'out', type: 'io' }, { id: '4', name: 'pow5v', state: 'out', type: 'power' }],
  [{ id: '5', name: 'gpio 3', state: 'out', type: 'io' }, { id: '6', name: 'gnd', state: 'out', type: 'ground' }],
  [{ id: '7', name: 'gpio 4', state: 'out', type: 'io' }, { id: '8', name: 'gpio uart', state: 'out', type: 'io' }],
  [{ id: '9', name: 'gnd', state: null, type: 'ground' }, { id: '10', name: 'gpio uart', state: 'out', type: 'io' }],
  [{ id: '11', name: 'gpio 17', state: 'out', type: 'io' }, { id: '12', name: 'gpio pcm', state: 'out', type: 'io' }],
  [{ id: '13', name: 'gpio 27', state: 'out', type: 'io' }, { id: '14', name: 'gnd', state: 'out', type: 'ground' }],
  [{ id: '15', name: 'gpio 22', state: 'out', type: 'io' }, { id: '16', name: 'gpio', state: 'out', type: 'io' }],
  [{ id: '17', name: 'pow3v3', state: null, type: 'power' }, { id: '18', name: 'gpio', state: 'out', type: 'io' }],
  [{ id: '19', name: 'gpio 10', state: 'out', type: 'io' }, { id: '20', name: 'gnd', state: 'out', type: 'io' }],
  [{ id: '21', name: 'gpio 9', state: 'out', type: 'io' }, { id: '22', name: 'gpio', state: 'out', type: 'io' }],
  [{ id: '23', name: 'gpio 11', state: 'out', type: 'io' }, { id: '24', name: 'gpio spi', state: 'out', type: 'io' }],
  [{ id: '25', name: 'gnd', state: null, type: 'ground' }, { id: '26', name: 'gpio spi', state: 'out', type: 'io' }],
  [{ id: '27', name: 'gpio 0', state: 'out', type: 'io' }, { id: '28', name: 'gpio i2c', state: 'out', type: 'io' }],
  [{ id: '29', name: 'gpio 5', state: 'out', type: 'io' }, { id: '30', name: 'gnd', state: 'out', type: 'ground' }],
  [{ id: '31', name: 'gpio 6', state: 'out', type: 'io' }, { id: '32', name: 'gpio', state: 'out', type: 'io' }],
  [{ id: '33', name: 'gpio 13', state: 'out', type: 'io' }, { id: '34', name: 'gnd', state: 'out', type: 'ground' }],
  [{ id: '35', name: 'gpio 19', state: 'out', type: 'io' }, { id: '36', name: 'gpio', state: 'out', type: 'io' }],
  [{ id: '37', name: 'gpio 26', state: 'out', type: 'io' }, { id: '38', name: 'gpio pcm', state: 'out', type: 'io' }],
  [{ id: '39', name: 'gnd', state: null, type: 'ground' }, { id: '40', name: 'gpio pcm', state: 'out', type: 'io' }],
];

const PinIcon = styled(CircleTwoTone)({});

export default function Deposits() {
  return (
    <React.Fragment>
      <ul>
        {gpio.map((row) => (
        <li
          key={`${row[0].id}:${row[1].id}`}
          sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
        ><Box sx={{ width: .5 }}>
          <a>
            <Typography fontSize={10}>{row[0].name}</Typography>
          </a>
          <span sx={{ borderRight: 1, backgroundColor: 'background' }} align="right"><PinIcon
            type={row[0].type}/></span>
        </Box>
          <Box sx={{ width: .5 }}>
            <span align="left"><PinIcon type={row[0].type}/></span>
            <a>
              <Typography fontSize={10}>{row[1].name}</Typography>
            </a>
          </Box>
        </li>
        ))}
      </ul>
    </React.Fragment>
);
}
